// ------------------------------------ geolocation ----------------------------------

function skipsMapApi() {
	var mapOb = initGeolocation();

	function onGeolocationChange() {
		console.log("onGeolocationChange");
		var geolocation = mapOb.geolocation;
		var coordinates      = toLonLat( geolocation.getPosition() );
		var longitude        = coordinates[0];
		var latitude         = coordinates[1];
		var accuracy         = geolocation.getAccuracy();
		var altitude         = geolocation.getAltitude();
		var altitudeAccuracy = geolocation.getAltitudeAccuracy();
		var heading          = geolocation.getHeading();
		var speed            = geolocation.getSpeed();

		var html = " ";
		html += "<b>" + latitude.toFixed(4) + "," + longitude.toFixed(4) + "</b>";
		if (!heading.isNaN()) {
			html += ", heading=<b>" + Math.round( heading * 180 / Math.PI ) + "</b>";
		}
		if (!speed.isNaN()) {
			html += ", speed=<b>" + (speed * 2.23694).toFixed(2) + "</b> mph";
		}
		if (!altitude.isNaN()) {
			html += ", elev=<b>" + altitude.toFixed(1) + "</b> m";
		}
		if (accuracy) {
			html += ", acc &plusmn;=" + Math.round(accuracy) + "/" + Math.round(altitudeAccuracy);
		}
		var dt = new Date();
		html += "<br/>\r\n" + dt.toString();

		document.getElementById("geo_info").innerHTML = html;
	}

	function onGeolocationError(error) {
		var info = document.getElementById("err_info");
		info.innerHTML = error.message;
		info.style.display = "";
	}

	function onGeolocationAccuracy() {
		var accuracyFeature = mapOb.accuracyFeature;
		accuracyFeature.setGeometry( mapOb.geolocation.getAccuracyGeometry() );
	}

	function initGeolocation() {
		mapOb = document.skipsMap || {};
		if (!mapOb.geolocation) {
			mapOb.geolocation = new Geolocation({
				projection: View.getProjection()
			});

			// events
			mapOb.geolocation.on("change", onGeolocationChange);
			mapOb.geolocation.on("error", onGeolocationError);
			mapOb.geolocation.on("change:accuracyGeometry", onGeolocationAccuracy);
			mapOb.geolocation.on("change:position", mapOb.position.onPositionChange);
		}

		document.skipsMap = mapOb;
		return mapOb;
	}
}

export default function skipsMapInitialize(latLngStr, latLngHandler) {
	console.log("skipsMapInitialize");
	if (document.skipsMap) return;

	document.skipsMap = {};
	var mapOb = document.skipsMap;

	mapOb.geocodeLatLngHandler = latLngHandler;
	mapOb.geocodeCountyHandler = null; //!!?? countyHandler;


	// Elements that make up the popup.
	var container = document.getElementById("popup");
	var content   = document.getElementById("popup-content");
	var closer    = document.getElementById("popup-closer");

	// click handler to hide the popup.
	closer.onclick = function() {
		document.skipsMap.overlay.setPosition(undefined);
		closer.blur();
		return false;
	};


	var myLonLat = parseLatLngToLonLat(latLngStr);
	if (!myLonLat) myLonLat = getLastLocation();

	if (myLonLat) {
		initializeMap(myLonLat);
	} else {
		console.log("no location.  Starting asynch initialization...");
		getCurrentLocation();
	}


	function parseLatLngToLonLat(latLngStr) {
		if (latLngStr) {
			var ll = latLngStr.split("%2C");
			if (ll.length != 2) ll = latLngStr.split(",");
			if (ll.length == 2) return [ ll[1]*1, ll[0]*1 ];
			alert("unable to parse latLng \"" + latLngStr + "\"");
		}
		return null;
	}

	function initializeMap(myLonLat) {
		console.log("initializeMap: starting at " + JSON.stringify(myLonLat, null, null));

		// ------------------------------------ map construction ----------------------------------

		var mapCoord = fromLonLat(myLonLat);
		var view = new View({
			center: mapCoord, //default projection = EPSG:3857
			zoom: 9,
			rotation: 0
		});

		var baseLayer = new TileLayer({
			source: new OSM()
		});

		mapOb.position = initializePosition();
		var currentPositionLayer = new VectorLayer({
			source: new VectorSource({
				features: [mapOb.position.accuracyFeature, mapOb.position.positionFeature]
			})
		});

		mapOb.markers = initializeMapMarkers();
		var markersLayer = new VectorLayer({
			source: new VectorSource({
				features: []
			})
		});
		mapOb.markers.markersLayer = markersLayer;

		mapOb.overlay = new Overlay({
			element: container
		});

		// widget constructor, extends module:ol/Control~Control
		var CurrentLocationControl = function (Control) {
			function CurrentLocationControl(opt) {
				var options = opt || {};

				var widget = document.createElement("button");
				widget.innerHTML = "\u2316";

				var widgetDiv = document.createElement("div");
				widgetDiv.className = "ol-currentlocation ol-unselectable ol-control";
				widgetDiv.appendChild(widget);

				Control.call(this, {
					element: widgetDiv,
					target: options.target
				});

				widget.addEventListener("click", getCurrentLocation, false);
			}

			if (Control) CurrentLocationControl.__proto__ = Control;
			CurrentLocationControl.prototype = Object.create( Control && Control.prototype );
			CurrentLocationControl.prototype.constructor = CurrentLocationControl;

			return CurrentLocationControl;
		}(Control);

		//ol.inherits(mapOb.generateWidget, ol.control.Control);  Openlayers 5 error

		mapOb.map = new Map({
			layers: [
				baseLayer,
				currentPositionLayer,
				markersLayer
			],
			overlays: [mapOb.overlay],
			target: "skipsMapPanel",
			controls: defaultControls().extend([
				new CurrentLocationControl({})
			]),
			view: view
		});

		mapOb.position.setCurrentPosition(mapCoord);

		mapOb.map.on("click", mapOb.markers.onMapClick);

		document.skipsMap = mapOb;

		console.log("initializeMap: map is initialized");
	}

	// ------------------------------------ markers and popups ----------------------------------

	function initializeMapMarkers() {

		var onMapClick = function (evt) {
			var mapCoord = evt.coordinate;
			console.log("onMapClick: " + JSON.stringify(mapCoord, null, null));
			var popupHtml = [];
			document.skipsMap.map.forEachFeatureAtPixel(evt.pixel, function (feature, layer) {
				console.log("onMapClick (forEachFeature): feature=" + feature + ", layer=" + layer);
				if (document.skipsMap.markers.markersLayer == layer) {
					//console.log("onMapClick (forEachFeature): IS MARKER LAYER, feature=" + feature);
					var props = feature.getProperties();
					popupHtml.push(props.contents);
				}
			});
			if (popupHtml.length > 0) {
				var html;
				if (popupHtml.length < 2) {
					html = popupHtml[0];
				} else {
					var maxLinks = 5;
					html = "<ul>";
					for (var i=0; i<popupHtml.length; i++) {
						if (i >= maxLinks) {
							html += "<li>...(" + (popupHtml.length - maxLinks) + " more)";
							break;
						}
						html += "<li>" + popupHtml[i];
					}
					html += "</ul>";
				}
				content.innerHTML = html;
				document.skipsMap.overlay.setPosition(mapCoord);
			} else {
				if (document.skipsMap.overlay.getPosition()) {
					closer.onclick();
				} else {
					var myLonLat = toLonLat(mapCoord);
					setLastLocation(myLonLat);
					document.skipsMap.position.moveCurrentPosition(mapCoord);
				}
			}
		};

		var addMarker = function (latLng, title, contents) {
			var lonLat = [latLng.lng, latLng.lat];
			var mapCoord = fromLonLat(lonLat);

			var markerFeature = new Feature();
			markerFeature.setStyle( getMarkerStyle(title, "red") );
			markerFeature.setGeometry( new Point(mapCoord) );
			var props = {"title":title, "contents":contents };
			markerFeature.setProperties(props);

			var markerSource = document.skipsMap.markers.markersLayer.getSource();
			markerSource.addFeature(markerFeature);
			var center = getCenter(markerSource.getExtent());
			document.skipsMap.map.getView().setCenter(center);
		};

		function getMarkerStyle(abbr, color) {
			var styles = [];
			styles.push( new Style({
					text: new Text({
						text: abbr,
						font: "Normal 10px Arial",
						fill: new Fill({color: color})
					}) /*,
					image: new Circle({
						radius: 1,
						stroke: new Stroke({color: "black", width: 1})
					}) */
				}));
			styles.push( new Style({
					image: new Circle({
						radius: 10,
						stroke:  new Stroke( {color: color, width: 2})
					})
				}));
			return styles;
		}

		var markers = {
			"onMapClick": onMapClick,
			"addMarker": addMarker,
		};
		return markers;
	}

	// ------------------------------------ current position handling ----------------------------------

	function initializePosition() {

		var moveCurrentPosition = function(mapCoord) {
			setCurrentPosition(mapCoord);

			// update UI controls
			var lonLat = toLonLat(mapCoord);
			var longitude = lonLat[0];
			var latitude  = lonLat[1];
			if (mapOb.geocodeLatLngHandler) mapOb.geocodeLatLngHandler(latitude, longitude);
			if (mapOb.geocodeCountyHandler) { // skipsGeocodeCountyHandler(foundState, foundCounty)
				var found = lookUpCounty(latitude, longitude);
				var foundState  = found.adm1.name;
				var foundCounty = found.adm2.name;
				//console.log("initializePosition: state=" + foundState + ", county=" + foundCounty);
				mapOb.geocodeCountyHandler(foundState, foundCounty);
			}

			function lookUpCounty(latitude, longitude) {
				// Google now blocks geocoding without an api key:
				//http://maps.googleapis.com/maps/api/geocode/json?latlng=40.714224,-73.961452&sensor=false
				//geocoder.geocode({"latLng": latLng}, geocodeCompleteHandler);
				// The following is a really bad approximation:

				var minDist = Number.MAX_VALUE;
				var found = {};
				var a = window.top.completeListOfCounties;
				for (var adm1Name in a) {
					var adm1 = a[adm1Name];
					var c = adm1.counties;
					for (var adm2Name in c) {
						var adm2 = c[adm2Name];
						var lat2 = adm2.lat, lon2 = adm2.lon;
						var dx = longitude - lon2, dy = latitude - lat2;
						var dist = dx*dx + dy*dy;
						if (minDist > dist) {
							minDist = dist;
							found["adm1"] = adm1; found["adm2"] = adm2;
						}
					}
				}
				console.log("lookUpCounty: state=" + found.adm1.name + ", county=" + found.adm2.name);
				return found;
			}
		}

		var setCurrentPosition = function(mapCoord) {
			var positionFeature = document.skipsMap.position.positionFeature
			document.skipsMap.map.getView().setCenter(mapCoord);
			positionFeature.setGeometry( new Point(mapCoord) );
		};

		var accuracyFeature = new Feature();

		var positionFeature = new Feature();
		positionFeature.setStyle( getPositionStyle() );

		var position = {
			"setCurrentPosition":  setCurrentPosition,
			"moveCurrentPosition": moveCurrentPosition,
			"accuracyFeature": accuracyFeature,
			"positionFeature": positionFeature
		};
		return position;


		function getPositionStyle() {
			var currentPositionStyle = new Style({
				image: new Circle({
					radius: 6,
					fill:   new Fill(   {color: "#3399CC"}),
					stroke: new Stroke( {color: "#fff", width: 2})
				})
			});
			return currentPositionStyle;
		}
	}

	function getCurrentLocation() {
		console.log("getCurrentLocation: getCurrentPosition...");
		navigator.geolocation.getCurrentPosition(currentLocationFoundHandler, currentLocationNotFoundHandler);
	}

	function currentLocationFoundHandler(pos) {
		var lat = pos.coords.latitude;
		var lon = pos.coords.longitude;
		console.log("found current position: " + lat + ", " + lon);
		var myLonLat = [lon,lat];
		if (document.skipsMap.map) {
			const mapCoords = fromLonLat(myLonLat);
			document.skipsMap.map.getView().animate({center: mapCoords, zoom: 10});
		} else {
			initializeMap(myLonLat);
		}
	}

	function currentLocationNotFoundHandler(err) {
		console.log("geolocation error: code " + err.code + " : " + err.message);
		console.log("using default map location");
		var lat = 45.489983;
		var lon = -122.694283;
		myLonLat = [lon,lat];
		if (document.skipsMap.map) {
			const mapCoords = fromLonLat(myLonLat);
			document.skipsMap.map.getView().animate({center: mapCoords, zoom: 10});
		} else {
			initializeMap(myLonLat);
		}
	}

	function setCookie(cname, cvalue, exdays) {
		var now = new Date();
		now.setTime(now.getTime() + exdays * 24 * 60 * 60 * 1000);
		var expires = "expires=" + now.toUTCString();
		document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
	}

	function getCookie(cname) {
		var name = cname + "=";
		var ca = document.cookie.split(';');
		for(var i = 0; i < ca.length; i++) {
			var c = ca[i];
			while (c.charAt(0) == ' ') {
				c = c.substring(1);
			}
			if (c.indexOf(name) == 0) {
				return c.substring(name.length, c.length);
			}
		}
		return "";
	}

	function getLastLocation() {
		var latLngStr = getCookie("loc");
		return parseLatLngToLonLat(latLngStr);
	}

	function setLastLocation(lonLatArr) {
		if (lonLatArr && lonLatArr.length == 2) {
			latLngStr = parseFloat(lonLatArr[1]) + "," + parseFloat(lonLatArr[0]);
			setCookie("loc", latLngStr, 365);
		}
	}
}

