import "./skipsBirdApi.css";
import "./skipsMapApi.css";
import "./skipsNav.css";
import skipsBirdApi       from "./skipsBirdApi.js";
import skipsMapInitialize from "./skipsMapApi.js";
import skipsNav           from "./skipsNav";

init();

function init()
{
	var nav = skipsNav();
	var res = skipsBirdApi();
	skipsMapInitialize(res.latLng, res.mapClickCallback);
	nav.closeNav();
}
